import React from 'react';
import Logo from "../../resources/dk-logo.jpg";
import { Flex, Heading, Img } from '@chakra-ui/react';
import { NavLink } from 'react-router-dom';

const NavbarLaptop = () => {
  return (
    <>
    <Flex justifyContent={'space-between'} p={[3, 2, 1, 2]} width={'95%'} margin="auto" alignItems={'center'} className='Navbar'>
      <NavLink to="/"><Img src={Logo} alt='' width={20} borderRadius={10}/></NavLink>
      <Flex gap={10}>
        <NavLink to="/"><Heading size={'md'} cursor="pointer">Home</Heading></NavLink>
        <NavLink to="/about"><Heading size={'md'} cursor="pointer">About</Heading></NavLink>
        <NavLink to="/skills"><Heading size={'md'} cursor="pointer">Skills</Heading></NavLink>
        <NavLink to="/projects"><Heading size={'md'} cursor="pointer">Projects</Heading></NavLink>
        <NavLink to="contact"><Heading size={'md'} cursor="pointer">Contact</Heading></NavLink>
      </Flex>
    </Flex>
    </>
  )
}

export default NavbarLaptop