import React from 'react';
import {GrLinkedinOption, GrGithub, GrTwitter} from "react-icons/gr";
import Pic from "../../resources/img.jpg";
import { Flex, Heading, Button, Img, Link } from '@chakra-ui/react';

const PortfollioMobile = () => {
  return (
    <>
    <Flex direction={'column'} gap={4}>
      <Heading>Hi 👋,</Heading>
      <Heading>I'am <span>Dinesh</span></Heading>
      <Heading>MERN Developer</Heading>
      <Button colorScheme={'blue'} width="140px" mt={6} onClick={()=> window.location="https://drive.google.com/file/d/1FS-AYR0uBiDlbuN3sJbmoZG-d2i1f_lg/view?usp=sharing"}>Resume</Button>
      
    </Flex>
    <Flex direction={'column'} alignItems='flex-end' justifyContent={'center'}>
      <Img src={Pic} alt='' width={60} height={60} borderRadius="50%" mt={24}/>
    </Flex>
    <Flex gap={4} mt={20}>
            <Link href='https://www.linkedin.com/in/dinesh-karde/'_hover={{"color": "#00ffff"}} ><GrLinkedinOption fontSize={26} /></Link>
            <Link href='https://github.com/captain-programming' _hover={{"color": "#00ffff"}}><GrGithub fontSize={26}/></Link>
            <Link href='https://twitter.com/DineshKarde103' _hover={{"color": "#00ffff"}}><GrTwitter fontSize={26}/></Link>
      </Flex>
    </>
  )
}

export default PortfollioMobile;